:root {
  /* Expanded Color Palette with Intelligent Contrast */
  --main-color: #005CA3;        /* Vibrant deep blue */
  --second-color: #b4defa;      /* Modern sky blue */
  --third-color: #1976D2;       /* Rich ocean blue */
  --accent-color: #4FC3F7;      /* Bright cyan-blue */
  --text-primary: #1F2937;      /* Deep charcoal */
  --text-secondary: #4B5563;    /* Soft charcoal */

  /* Enhanced Neutral Palette */
  --neutral-100: #F9FAFB;       /* Pristine white */
  --neutral-200: #F3F4F6;       /* Light gray */
  --neutral-300: #E5E7EB;       /* Soft border gray */
  --neutral-400: #D1D5DB;       /* Medium gray */
  --neutral-600: #6B7280;       /* Muted text gray */
  --neutral-800: #111827;       /* Almost black */

  /* Advanced Glassmorphism & Depth */
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.5);
  --glass-shadow: rgba(0, 92, 163, 0.12);

  /* Sophisticated Typography */
  --font-primary: 'Inter', 'system-ui', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  
  /* Refined Shadow System */
  --shadow-subtle: 0 2px 4px rgba(0, 0, 0, 0.06);
  --shadow-light: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-medium: 0 8px 15px rgba(0, 92, 163, 0.12);
  --shadow-heavy: 0 15px 25px rgba(0, 92, 163, 0.15);

  /* Advanced Transition Curves */
  --transition-standard: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-bounce: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

[data-theme="watermelon"] {
  color-scheme: light dark;
  scroll-behavior: smooth;
  accent-color: var(--main-color);
}

[data-theme="watermelon"] body {
  font-family: var(--font-primary);
  font-size: 15px;
  line-height: 1.6;
  background: linear-gradient(135deg, var(--neutral-100), var(--neutral-200));
  color: var(--text-primary);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Intelligent Interactive Elements */
[data-theme="watermelon"] a {
  color: var(--third-color);
  text-decoration: none;
  transition: var(--transition-standard);
  position: relative;
  overflow: hidden;
}

[data-theme="watermelon"] a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--accent-color);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

[data-theme="watermelon"] a:hover::after {
  transform: scaleX(1);
}

/* Intelligent Navigation */
[data-theme="watermelon"] .navbar {
  background: linear-gradient(
    135deg, 
    var(--main-color), 
    var(--second-color), 
    var(--third-color), 
    var(--accent-color)
  );
  background-size: 400% 400%;
  animation: GradientAnimation 15s ease infinite;
  box-shadow: var(--shadow-medium);
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
  border-bottom: 1px solid rgba(255,255,255,0.1);
}

@keyframes GradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

[data-theme="watermelon"] .navbar-nav > li > a {
  color: white;
  font-weight: 600;
  letter-spacing: 0.5px;
  position: relative;
}

[data-theme="watermelon"] .navbar-nav > li > a::before {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--accent-color);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

[data-theme="watermelon"] .navbar-nav > li > a:hover::before {
  transform: scaleX(1);
}

/* Intelligent Sidebar */
[data-theme="watermelon"] .standard-sidebar-item {
  border-radius: 12px;
  background: var(--glass-bg);
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
  border: 1px solid var(--neutral-300);
  box-shadow: var(--shadow-subtle);
  transition: var(--transition-standard);
  will-change: transform, box-shadow;
}

[data-theme="watermelon"] .standard-sidebar-item.selected {
  background: linear-gradient(to right, var(--main-color), var(--second-color));
  color: white;
  box-shadow: var(--shadow-medium);
}

[data-theme="watermelon"] .standard-sidebar-item:hover {
  transform: translateX(8px) scale(1.02);
  box-shadow: var(--shadow-light);
}

/* Intelligent Buttons */
[data-theme="watermelon"] .btn-primary {
  background: linear-gradient(135deg, var(--main-color), var(--accent-color));
  color: white;
  font-weight: 600;
  border-radius: 10px;
  letter-spacing: 0.5px;
  transition: var(--transition-bounce);
  box-shadow: var(--shadow-light);
  position: relative;
  overflow: hidden;
}

[data-theme="watermelon"] .btn-primary::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(120deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: all 0.5s ease;
}

[data-theme="watermelon"] .btn-primary:hover::after {
  left: 100%;
}

[data-theme="watermelon"] .btn-primary:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-heavy);
}

/* Intelligent Form Inputs */
[data-theme="watermelon"] input,
[data-theme="watermelon"] select,
[data-theme="watermelon"] textarea {
  border: 1px solid var(--neutral-300);
  border-radius: 10px;
  transition: var(--transition-standard);
  box-shadow: var(--shadow-subtle);
}

[data-theme="watermelon"] input:focus,
[data-theme="watermelon"] select:focus,
[data-theme="watermelon"] textarea:focus {
  border-color: var(--main-color);
  box-shadow: 0 0 0 3px rgba(0, 92, 163, 0.15);
  outline: none;
}

/* Responsive Typography */
@media (max-width: 768px) {
  [data-theme="watermelon"] body {
    font-size: 14px;
  }
}
